@mixin clamp($bb) {
  display: -webkit-box;
  -webkit-line-clamp: $bb;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

.templateSearch {
	.page-header {
		padding: 60px 0;
		.page-heading {
			margin-bottom: 15px;
			font-size: 24px;
		}
		.no-result {
			margin: 0 0 30px;
		}
	}
	.search-form-wrapper {
		max-width: 800px;
		margin: 35px auto 0;
		position: relative;
		.form-search-container {
			position: unset;
		}
		.no-result {
			flex: 0 0 100%;
			max-width: 100%;
			@include text-left;
			border: 0;
		}
		form {
			display: flex;
			align-items: center;
			background: var(--header-form-bg-color);
			border-radius: var(--border-radius);
			button {
				display: flex;
			}
			svg {
				width: 16px;
				height: 16px;
				path {
					stroke: var(--primary-color);
				}
			}
		}
		input {
			border: 0;
		}
	}
	.result-container {		
		.search-result-list {
			max-height: 100%;
			padding-bottom: 50px;
			li {
				padding: 15px 0;
				background: none;
			}
			.item-content {
				@include clamp(3);
				color: var(--subtext-color);
				display: flex;
				border: 0;
			}
		}
	}
	.result-counting {
		margin: 0 0 40px;
	}
	@media (min-width: 750px) {
		.result-container {			
			.search-result-list {
				display: flex;
				flex-wrap: wrap;
				li {
					flex: 0 0 50%;
					max-width: 50%;	
					padding-left: 15px;
					padding-right: 15px;			
				}
			}
		}
	}
}